.container {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
}

.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  object-fit: fill;
}

.countdown {
  grid-area: 2 / 1 / 3 / 2;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: row;
  gap: 30px;
  align-items: center;
  justify-content: center;
}

.box {
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px solid white;
}

.time_text {
  font-size: 100px;
  color: white;
}

.descrip_text {
  color: #A3A3A3;
  font-size: 30px;
}